import Connection from '../base/connection';
import RegionConnectionView from './regionConnectionView';

class RegionConnection extends Connection {
  constructor (options) {
    super(options);
    this.type = 'region';
  }

  render () {
    this.view = new RegionConnectionView(this);
  }
}

export default RegionConnection;



